<script>
// import download from "downloadjs";
import Vue from "vue";
import JsonExcel from "vue-json-excel";
Vue.component("downloadExcel", JsonExcel);

export default {
  name: "admin",
  async created() {
    await this.setSelect();
  },
  data() {
    return {
      root: true,
      user: false,
      mode: {
        mode: "cors",
        headers: {
          "Content-type": "application/json; charset=UTF-8",
          "Access-Control-Allow-Origin": "*",
        },
      },
      username: "",
      password: "",
      json_data: [],
      centre: [],
      course: [],
      json_1: [],
      id: null,
      subid: null,
      jsonFetched: false,
      json: [],
      field: {
        // id
        // - course
        centre: "centre",
        course: "course",
        // area
        region: "area.region",
        district: "area.district",
        //- Register
        name: "register.name",
        category: "register.category",
        email: "register.email",
        number: "register.number",
        gender: "register.gender",
        "Qualification name": "register.qualification.name",
        "Qualification course": "register.qualification.course",
        "Stream Name": "register.stream.name",
        "Stream Other": "register.stream.other",
        "Current Year": "register.stream.year",
        "Designation Name": "register.designation.name",
        "Designation Other": "register.designation.other",
        Teaching: "register.teaching",
        // inst
        "Institute.University": "institute.university",
        "Institute.Type": "institute.uni_type",
        "Institute.Name": "institute.name",
        "Institute.Address": "institute.address",
        "Institute.Head": "institute.head",
        "Institute.Number": "institute.number",
        "Institute.Email": "institute.email",
        "Institute.Expectation": "institute.expectation",
        Date: "date",
      },
      // sample
      admin: {
        username: "admin",
        password: 1413914,
      },
    };
  },
  methods: {
    setTab(role) {
      if (role == "root") {
        this.root = true;
        this.user = false;
      } else if (role == "user") {
        this.root = false;
        this.user = true;
      }
    },
    async submitAdmin() {
      // console.log(this.id)
      //- check username & pasw
      // console.log(`Username : ${this.username} \nPassword : ${this.password}`);
      //- chk
      let chk =
        this.admin.username == this.username &&
        this.admin.password == this.password;

      //- get ID & subid
      // let xlsx = require("json-as-xlsx")
      if (chk) {
        // fetch
        let url = "";
        if (this.root) {
          url = this.$store.state.api_user;
        } else {
          url = `${this.$store.state.api_user}/${this.id}/${this.subid}`;
        }
        let raw = await fetch(url, this.mode);
        let data = await raw.json();
        // if reg.lenth == 1 then json_1_data
        // if(data.register.length)

        data.forEach((x) => {
          //- 1
          x.data.register.forEach((y) => {
            this.json_data.push({
              course: x.data.course,
              centre: x.data.centre,
              area: {
                region: x.data.area.region,
                district: x.data.area.district,
              },
              register: {
                name: y.name,
                category: y.category,
                email: y.email,
                number: y.number,
                gender: y.gender,

                qualification: {
                  name: y.qualification.name,
                  course: y.qualification.course,
                },
                stream: {
                  name: y.stream.name,
                  other: y.stream.other,
                  year: y.stream.year,
                },
                designation: {
                  name: y.designation.name,
                  other: y.designation.other,
                },
                teaching: y.teaching,
              },
              institute: {
                university: x.data.institute.university,
                uni_type: x.data.institute.uni_type,
                name: x.data.institute.name,
                address: x.data.institute.address,
                head: x.data.institute.head,
                number: x.data.institute.number,
                email: x.data.institute.email,
                expectation: x.data.institute.expectation,
              },
              date: x.data.date,
            });
          });
          //- 2
          // this.json_1.push(x.data);
        });
        // console.log(this.json_data)

        this.jsonFetched = true;
        // console.log(this.json_1);

        // download(JSON.stringify(this.json_1), "apidata.json", "text/plain");
      }
    },
    async setSelect() {
      let raw = await fetch(this.$store.state.api, this.mode);
      let json = await raw.json();
      json.forEach((cen) => {
        // centre
        this.centre.push({
          id: cen.id,
          name: cen.program,
        });
        // course:[]
        this.centre[cen.id - 1].course = [];
        // course.push()
        if (cen.subprogram != undefined) {
          cen.subprogram.forEach((cou) => {
            this.centre[cen.id - 1].course.push({
              id: cou.id,
              name: cou.program,
            });
          });
        }
      });
    },
    setCourse() {
      this.centre.forEach((ele) => {
        if (ele.id == this.id) {
          this.course = ele.course;
        }
      });
    },
  },
};
</script>
